.styles-switcher {
  position: relative;
  min-width: 56px;
  height: 26px;
  -webkit-appearance: none;
  background: transparent;
  outline: none;
  border-radius: 20px;
  border: 1px solid #d1dee9;
  transition: .5s;
}

.styles-switcher:checked {
  background: #4fa5f7;
}

.styles-switcher:checked:before {
  left: 30px;
  background: #fff;
}

.styles-switcher:before {
  content: '';
  position: absolute;
  width: 20px;
  height: 20px;
  border-radius: 20px;
  top: 2.3px;
  left: 4px;
  background: #7f87a5;
  transition: .5s;
}

.check {
  margin-top: 20px;
  padding-left: 20px;
}

.check label {
  position: relative;
}

.check input[type=checkbox] {
  display: none;
}

.check input[type=checkbox] + label:before {
  content: "\2713";
  color: transparent;
  display: inline-block;
  border: 1px solid #e1eef6;
  font-size: 14px;
  line-height: 13px;
  margin: -5px 5px 0 0;
  background: #f9fcfe;
  height: 14px;
  width: 14px;
  text-align: center;
  vertical-align: middle;
  transition: color ease .3s;
  position: absolute;
  left: -20px;
  top: 6px;
}

.check input[type=checkbox]:checked + label:before {
  color: #4e5983;
}

